import React from "react";
import ReactCookieConsent from "react-cookie-consent";
import { RichText } from "prismic-reactjs";
import { Typography, Link } from "@material-ui/core";

import StyledComponent from "./CookieConsent.style";

const CookieConsent = ({ cookieConsent }) => {
  if (!cookieConsent) {
    return null;
  }

  return (
    <StyledComponent>
      <ReactCookieConsent
        location="bottom"
        buttonText="Accept"
        buttonStyle={{ display: "none" }}
        contentStyle={{ textAlign: "center", margin: "0.35rem" }}
        acceptOnScrollPercentage={5}
        acceptOnScroll
      >
        <Typography variant="body2">
          {RichText.asText(cookieConsent.text)}{" "}
          {cookieConsent.link_text && cookieConsent.link && (
            <Link
              variant="body2"
              color="textPrimary"
              href={cookieConsent.link.url}
              target={cookieConsent.link.target}
            >
              {RichText.asText(cookieConsent.link_text)}
            </Link>
          )}{" "}
          {RichText.asText(cookieConsent.description)}
        </Typography>
      </ReactCookieConsent>
    </StyledComponent>
  );
};

CookieConsent.defaultProps = {
  cookieConsent: null,
};

CookieConsent.propTypes = {};

export default CookieConsent;
