import React from "react";
import PropTypes from "prop-types";
import { RichText } from "prismic-reactjs";
import { useMediaQuery, Container, Grid, Typography } from "@material-ui/core";

import { Image, ResponsiveImage } from "@landingpage/core/src/components/Image";
import UspList from "./UspList";
import CtaList from "./CtaList";

import StyledComponent from "./Header.style";

const Content = ({ header }) => {
  const isMobile = useMediaQuery((theme) => theme.breakpoints.down("sm"));

  return (
    <Grid
      container
      direction="row"
      justify="space-between"
      alignItems="stretch"
    >
      <Grid className="text" item xs={12} sm={12} md={6} lg={6} xl={6}>
        <Grid container direction="column" spacing={4}>
          <Grid className="mobile-center" item>
            <Typography className="title" variant="h1">
              {RichText.asText(header.title)}
            </Typography>
            {isMobile && (
              <Grid item>
                <Image image={header.image} />
              </Grid>
            )}
            <Typography
              className="description"
              variant="body1"
              component="div"
              gutterBottom
            >
              {RichText.render(header.description)}
            </Typography>
          </Grid>

          {header.unique_selling_points.length > 0 && (
            <Grid item>
              <UspList items={header.unique_selling_points} />
            </Grid>
          )}

          <Grid className="mobile-center call-to-action" item>
            <CtaList items={header.call_to_actions} />
          </Grid>
        </Grid>
      </Grid>
      {!isMobile && (
        <Grid className="imageCenter" item xs={12} sm={12} md={6} lg={6} xl={6}>
          <div className="deviceImage">
            <ResponsiveImage image={header.image} />
          </div>
        </Grid>
      )}
    </Grid>
  );
};

const Header = ({ header }) => {
  if (!header) return null;

  return (
    <StyledComponent>
      <Container maxWidth="xl" disableGutters>
        <React.Fragment>
          <Content header={header} />
        </React.Fragment>
      </Container>
    </StyledComponent>
  );
};

Header.defaultProps = {
  header: null,
};

Header.propTypes = {
  header: PropTypes.object.isRequired,
};

export default Header;
