import styled from "styled-components";

const StyledComponent = styled.header`
  background-color: ${({ theme }) => theme.palette.background.header};
  padding: ${({ theme }) =>
    `${theme.spacing(10)}px ${theme.spacing(5)}px 0px ${theme.spacing(5)}px`};

  .description,
  .description > p {
    margin-bottom: 0;
    color: #ffffff;
  }

  .MuiTypography-h1 {
    color: #ffffff;
  }

  .imageCenter {
    display: flex;
    justify-content: center;
    align-items: center;

    .deviceImage {
      width: 100%;
      margin-bottom: -30px;
    }
  }

  .text {
    background-color: ${({ theme }) => theme.palette.background.header};
    padding: ${({ theme }) => `${theme.spacing(8)}px ${theme.spacing(6)}px`};
  }

  ${({ theme }) => theme.breakpoints.down("md")} {
    .text {
      padding: ${({ theme }) => `${theme.spacing(8)}px ${theme.spacing(4)}px`};
    }
  }

  ${({ theme }) => theme.breakpoints.down("sm")} {
    padding: ${({ theme }) =>
      `${theme.spacing(4)}px 0px ${theme.spacing(4)}px 0px`};

    .text {
      background: transparent;
      padding: ${({ theme }) => `0px ${theme.spacing(3)}px`};
    }

    .call-to-action {
      margin-top: ${({ theme }) => `${theme.spacing()}px`};
    }

    .gatsby-image-wrapper {
      margin: ${({ theme }) => `${theme.spacing(3)}px 0px`};
    }
  }
`;

export default StyledComponent;
