import { graphql } from "gatsby";

export const FaqFragment = graphql`
  fragment FaqFragment on PRISMIC_HomepageBodyFaq {
    type

    primary {
      faq_title
      cta_title
      cta_link {
        ... on PRISMIC__ExternalLink {
          url
          target
        }
      }
    }

    fields {
      faq_question
      faq_description
    }
  }
`;
