import React from "react";
import PropTypes from "prop-types";
import { RichText } from "prismic-reactjs";

import Button from "@landingpage/core/src/components/Button";

const CtaList = ({ items }) => {
  return (
    <>
      {items.map((callToAction, index) => (
        <Button
          key={index}
          color="primary"
          href={callToAction.cta_link.url}
          target={callToAction.cta_link.target}
        >
          {RichText.asText(callToAction.cta_title)}{" "}
        </Button>
      ))}
    </>
  );
};

CtaList.defaultProps = {
  items: [],
};

CtaList.propTypes = {
  items: PropTypes.array.isRequired,
};

export default CtaList;
