import React from "react";
import { graphql, StaticQuery } from "gatsby";
import { withPreview } from "@prismicio/gatsby-source-prismic-graphql";
import { RichText } from "prismic-reactjs";
import { get } from "lodash";

import { CookieConsentFragment } from "@landingpage/core/src/components/CookieConsent/fragment";
import { TopbarFragment } from "@landingpage/core/src/components/Topbar/fragment";
import { FooterFragment } from "@landingpage/core/src/components/Footer/fragment";
import { HomepageMovieSliderFragment } from "@landingpage/core/src/components/Movies/fragment";
import { PricingFragment } from "@landingpage/core/src/components/Pricing/fragment";
import { FaqFragment } from "@landingpage/core/src/components/Faq/fragment";

import { HeaderFragment } from "../components/Header/fragment";
import { HomepageContentBlockFragment } from "../components/ContentBlock/fragment";
import { ContentWithQuoteBoxFragment } from "../components/ContentWithQuoteBox/fragment";

import Layout from "@landingpage/core/src/components/Layout";
import SEO from "@landingpage/core/src/components/SEO";
import CookieConsent from "@landingpage/core/src/components/CookieConsent";
import Footer from "@landingpage/core/src/components/Footer";
import Topbar from "@landingpage/core/src/components/Topbar";

import Header from "../components/Header";
import Slices from "../components/Slices";

const query = graphql`
  query HomepageById($id: String) {
    prismic {
      allHomepages(id: $id) {
        edges {
          node {
            _meta {
              id
              lang
              lastPublicationDate
            }

            title

            cookie_consent {
              ...CookieConsentFragment
            }
            topbar {
              ...TopbarFragment
            }
            header {
              ...HeaderFragment
            }
            footer {
              ...FooterFragment
            }

            body {
              ...HomepageMovieSliderFragment
              ...HomepageContentBlockFragment
              ...ContentWithQuoteBoxFragment
              ...PricingFragment
              ...FaqFragment
            }
          }
        }
      }
    }
  }
`;

const renderHomepage = (data) => {
  const edges = get(data, "prismic.allHomepages.edges", []);
  const page = get(edges, "0.node");
  if (!page) return null;

  return (
    <Layout>
      <SEO lang={page._meta.lang} title={RichText.asText(page.title)} />
      <CookieConsent cookieConsent={page.cookie_consent} />
      <Topbar topbar={page.topbar} logo={{ width: 140, height: 24 }} />
      <Header header={page.header} />
      <Slices slices={page.body} />
      <Footer
        footer={page.footer}
        publicationDate={page.lastPublicationDate}
        logo={{ width: 140, height: 24 }}
        borderTop
      />
    </Layout>
  );
};

const Homepage = () => (
  <StaticQuery
    query={`${query}`}
    render={withPreview(renderHomepage, query, [
      CookieConsentFragment,
      TopbarFragment,
      HeaderFragment,
      FooterFragment,
      HomepageMovieSliderFragment,
      HomepageContentBlockFragment,
      ContentWithQuoteBoxFragment,
      PricingFragment,
      FaqFragment,
    ])}
  />
);
export default Homepage;
