import { graphql } from "gatsby";

export const HeaderFragment = graphql`
  fragment HeaderFragment on PRISMIC_Header {
    title
    description
    image

    unique_selling_points {
      usp_title
    }
    call_to_actions {
      cta_title
      cta_link {
        ... on PRISMIC__ExternalLink {
          url
          target
        }
      }
    }
  }
`;
