import { graphql } from "gatsby";

export const HomepageMovieSliderFragment = graphql`
  fragment HomepageMovieSliderFragment on PRISMIC_HomepageBodyMovie_slider {
    type

    primary {
      movie_text
      movie_title
      cta_title
      cta_color
      cta_link {
        ... on PRISMIC__ExternalLink {
          url
          target
        }
      }
    }

    fields {
      collection_id
      collection_text
      collection_title
    }
  }
`;

