import { graphql } from "gatsby";

export const FooterFragment = graphql`
  fragment FooterFragment on PRISMIC_Footer {
    cta_title
    cta_link {
      ... on PRISMIC__ExternalLink {
        url
        target
      }
    }

    links {
      title
      link {
        ... on PRISMIC__ExternalLink {
          url
          target
        }
      }
    }
  }
`;
