import { graphql } from "gatsby";

export const HomepageContentBlockFragment = graphql`
  fragment HomepageContentBlockFragment on PRISMIC_HomepageBodyContent1 {
    type

    primary {
      content_title
      content_description

      quote_text
      quote_person
      grid_xl
      grid_large
      grid_medium
      image_direction
      image_type
      read_more_button
      read_more_text
      read_less_text

      content_image
    }

    fields {
      cta_title
      cta_color
      cta_link {
        ... on PRISMIC__ExternalLink {
          url
          target
        }
      }
    }
  }
`;

