import { graphql } from "gatsby";

export const PricingFragment = graphql`
  fragment PricingFragment on PRISMIC_HomepageBodyPricing {
    type

    primary {
      pricing_title
      image

      monthly_title
      monthly_ribbon
      monthly_price

      yearly_title
      yearly_ribbon
      yearly_price

      cta_title
      cta_link {
        ... on PRISMIC__ExternalLink {
          url
          target
        }
      }
      selected_plan
      currency_symbol
    }

    fields {
      usp_title
    }
  }
`;
